.popup__container {
    box-sizing: border-box;
    max-width: 430px;
    width: 100%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    margin: auto;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    position: relative;
    padding: 34px 36px 37px;
    z-index: 2;
  }
  
  @media screen and (max-width: 1023px) {
    .popup__container {
      width: calc(100% - 38px);
      padding: 25px 22px;
    }
  }