.popup__fullscreen {
    position: fixed;
    visibility: hidden;
    justify-content: center;
    align-items: center;
    margin: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0.5;
    /* transition: visibility 0s, opacity 0.5s linear; */
}

